}
.newGameModal {
  width: 80%;
  margin: auto;
  margin-top: 10%;
  background-color: rgba(255, 255, 255, 0.9);
  border: 5px solid #3f51b5;
  border-radius:10px;
  padding: 10px;
  outline: none;
}
